/* eslint-disable no-undef */
document.addEventListener('DOMContentLoaded', () => {
  const targetId = 'burger-controll-';

  const openButton = document.getElementById(`${targetId}button-open`);
  const closeButton = document.getElementById(`${targetId}button-close`);
  const modal = document.getElementById(`${targetId}modal`);
  const backdrop = document.getElementById(`${targetId}modal-backdrop`);

  const toogleClasses = () => {
    openButton.classList.toggle('hidden');
    closeButton.classList.toggle('hidden');
    modal.classList.toggle('hidden');
  };

  const controlBurgerDisplay = () => {
    toogleClasses();
  };

  openButton?.addEventListener('touchstart', controlBurgerDisplay);
  closeButton?.addEventListener('touchstart', controlBurgerDisplay);
  closeButton?.addEventListener('touchstart', controlBurgerDisplay);
  backdrop?.addEventListener('touchstart', controlBurgerDisplay);
});
